@use "global" as *;

.voice {
    background: $c-base;
}

.voice__inner {
    padding-top: rclamp(45, 80, 390, 1440);
    padding-bottom: rclamp(70, 168, 390, 1440);
}

.voice__title {
    max-width: 177px;
}

.voice__swiper {
    margin-top: rclamp(50, 80, 390, 1440);
    max-width: 1130px;
}

.voice__slide {
    padding-inline: 80px;

    @include mq("md") {
        padding-inline: 9px;
    }
}

.voice__card {
    border-radius: 16px;
    box-shadow: 0px 4px 4px 0px rgba(199, 218, 255, 0.25);
    overflow: hidden;
    width: 235px;
}

.voice__image {
    img {
        height: 170px;
    }
}

.voice__body {
    background: $c-white;
    overflow: hidden;
    padding: 12px 14px 9px 9px;
}

.voice__lead {
    font-size: rclamp(15, 18, 390, 1440);
    line-height: normal;
}

.voice__text {
    margin-top: 16px;
    line-height: normal;
    display: none;
}

.voice__footer {
    margin-top: rclamp(27, 32, 390, 1440);
    display: flex;
    justify-content: space-between;
    width: 100%;
}

.voice__info {
    font-size: 11.755px;
}

.voice__icon {
    width: rclamp(16, 22, 390, 1440);
    height: rclamp(16, 22, 390, 1440);
}

.voice__icon--close {
    display: none;
}

.voice__pagination {
    margin-top: rclamp(15, 80, 390, 1440);
}

.swiper-horizontal>.swiper-pagination-bullets, .swiper-pagination-bullets.swiper-pagination-horizontal, .swiper-pagination-custom, .swiper-pagination-fraction {
    bottom: auto;
}

.voice__prev,
.voice__next {
    top: 46%;
}

.voice__prev {
    left: 25px;
}

.voice__next {
    right: 25px;
}
